import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"

import styles from './style.module.scss';

interface State {
  geometry: string
  identifier: string
  isPollinationModel: boolean
  isFocused: boolean
  syncToken: string
}

declare global {
  interface Window {
    chrome: any;
  }
}

class GetGeometry extends StreamlitComponentBase<State> {
  public state = { geometry: "", 
  identifier: "",
  isPollinationModel: false,
  isFocused: false,
  syncToken: "" }

  public render = (): ReactNode => {

    const imgStyle = {
      margin: "0px 3px 2px 0px"
    }

    return (
      <span>
        <button
          className={styles.poButton}
          onClick={this.onClicked}
          disabled={this.props.disabled}
          onFocus={this._onFocus}
          onBlur={this._onBlur}
        >
          <span>
            <img style={imgStyle} 
            src={process.env.PUBLIC_URL + '/img/pollination.png'} 
            alt="logo" />
          </span>
          Get { this.props.args["isPollinationModel"] ? 
            "Pollination Model" : 
            "Geometry" } 
            { this.props.args["syncToken"] && 
            " (Sync)" }
        </button>
      </span>
    )
  }

  componentDidUpdate() {
    // Empty identifier is not allowed
    if (this.state.identifier === "")
      return;
    
    // Same synctoken is not allowed
    if (this.props.args["syncToken"] 
      === this.state.syncToken )
      return;

    // Try get array GUIDs
    const stateIdentifiers = JSON
      .parse(this.state.identifier) as Array<string>;

    // Empty GUIDs
    if (!stateIdentifiers)
      return;
    
    // Try get the key GUID as trigger
    const id = this.props.args["syncToken"]
      .split(":", 1)[0];
    
    if (stateIdentifiers.includes(id)
      && this.state.syncToken 
      !== this.props.args["syncToken"])
    {
      const newState = this.dotNetRun();
      this.setStateNow(newState);
    }
    console.log('[POLLINATION-DEBUG]: auto-update.');
  }

  private setStateNow = (newState: State): 
    void => {
    this.setState(
      (prevState) => ({ 
        ...prevState, 
        geometry: newState.geometry,
        identifier: newState.identifier,
        isPollinationModel: newState.isPollinationModel,
        syncToken: newState.syncToken
      }),
      () => Streamlit.setComponentValue(this.state.geometry)
    );
    console.log('[POLLINATION-DEBUG]: JSON string sent.');
  }

  private dotNetRun = (selection: boolean = false): State => {
    if (typeof window.parent.chrome != "undefined") {
      if (typeof window.parent.chrome.webview == "undefined")
      {
        console.log('[POLLINATION-DEBUG]: webview not found.');
        return this.state;
      }

      // DotNet invoke
      let message = null;
      if (!this.props.args["isPollinationModel"]) {
        message = window.parent.chrome.webview
        .hostObjects.sync.geometry.PassJsonGeometry(
          this.props.args["key"], 
          selection);
      } else {
        message = window.parent.chrome.webview
        .hostObjects.sync.hbjson.PassPollinationModel(
          selection);
      }
      if (message == null)
        return this.state;
  
      var newState = {
        geometry: message.Geometry,
        identifier: message.Identifier,
        isPollinationModel: this.props.args["isPollinationModel"],
        syncToken: this.props.args["syncToken"]
      } as State;
  
      return newState;
    }
    else
    {
      console.log('[POLLINATION-DEBUG]: chrome not found.');
      return this.state;
    }
  }

  private onClicked = (): void => {
    // Run with selection enabled
    // Geometries, list of GUIDs saved on runtime
    // Pollination Model, Model GUID
    const newState = this.dotNetRun(true);
    this.setStateNow(newState);
  }

  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

export default withStreamlitConnection(GetGeometry)
